.doctors {
  display: grid;
  height: 100vh;
  grid-template-rows: 200px auto;
  grid-template-areas:
    "filter"
    "cards";
  grid-gap: 10px;
}
.filter {
  grid-area: filter;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  /* border: 1px solid black; */
}
.filter > input {
  margin-right: 2rem;
  padding: 10px 15px;
  width: 60%;
}
.filter > input:focus {
  border: none;
  /* border-bottom: 1px solid black; */
  text-decoration: underline;
  font-size: 0.8rem;
  font-weight: bold;
}
.filter > button {
  padding: 10px 15px;
  background-color: #2b6fc9;
  color: white;
  margin-right: 1rem;
}
.filter > button:hover {
  transform: scale(1.1);
}
.filter > select {
  padding: 10px 17px;
  margin-right: 1rem;
}
.cards {
  padding-left: 7px;
  grid-area: cards;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  grid-auto-rows: 500px;
  /* grid-column-gap: 0.5rem; */
  grid-row-gap: 2rem;
}

@media (max-width: 760px) {
  .filter {
    flex-direction: column;
    justify-content: space-evenly;
  }
}
