.card {
  border: 1px solid black;
  border-radius: 8px;
  /* padding: 10px 0px; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 380px;
  background-color: #f4f4f4;
  /* min-width: fit-content; */
}
.upper {
  flex: 0.6;
}
.upper > img {
  max-width: 380px;
  margin-bottom: 1rem;
  overflow: hidden;
  max-height: 350px;
  width: 380px;
  height: 300px;
}
.lower {
  flex: 0.4;
}

.lower > p {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 300;
  font-size: 1rem;
  margin-bottom: 0.5rem;
}

.lower > button {
  padding: 10px 15px;
  background-color: #2b6fc9;
  color: white;
  margin-bottom: 0.2rem;
  margin-top: 1rem;
}

.lower > button:hover {
  transform: scale(1.1);
}
