.navBar {
  width: 100%;
  height: 100px;
  background-color: #171717;
  display: flex;
  justify-content: space-around;
  align-items: center;
  /* padding: 0.3rem 0.1rem; */
  z-index: 50;
  padding-left: 3rem;
  padding-right: 3rem;
}
.navBar_logo h2 {
  color: #f4f4f4;
  font-size: 1.4rem;
  cursor: pointer;
}

.navBar_links {
  display: flex;
  list-style: none;
  align-items: center;
}

.navBar_links > li > a {
  text-decoration: none;
  color: #f4f4f4;
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  margin-left: 1rem;
  margin-right: 0.5rem;
  padding: 10px;
  border-radius: 8px;
}

.navBar_links > li > a:hover {
  background-color: #dd219e;
  transform: scale(1.1);
}

.navBar_links > li > a > span {
  display: flex;
  align-items: center;
  margin-left: 8px;
}

.navBar_hamburger {
  width: 30px;
  height: 30px;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.navBar_hamburger > div {
  width: 100%;
  height: 3px;
  background-color: #f4f4f4;
}

.navBar_hamburger:hover > div {
  background-color: #dd219e;
}

@media (max-width: 960px) {
  .navBar_links {
    display: none;
  }
  .navBar_hamburger {
    display: flex;
  }
}

@media (max-width: 500px) {
  .navBar_logo h2 {
    font-size: 1rem;
  }
}
